body {
  color: #e0e0e0;
  text-align: center;
  background-color: #121212;
  font-family: Arial, sans-serif;
}

h1 {
  color: #fff;
}

.items-list {
  justify-content: center;
  gap: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

.item {
  background-color: #1e1e1e;
  border-radius: 10px;
  width: 250px;
  padding: 15px;
  box-shadow: 0 0 10px #ffffff1a;
}

.item-title {
  color: #bb86fc;
  margin-bottom: 10px;
  font-size: 20px;
}

.item-price {
  color: #03dac6;
  font-weight: bold;
}

.item-description {
  color: #b0b0b0;
  font-size: 14px;
}
